import React from 'react';
import portfolioData from '../data/portfolioData';
import PortfolioComponent from '../components/portfolio/Portfolio.component';

const Portfolio = () => {
  return (
    <div className="my-6 px-5 max-w-6xl w-full ">
      <div className="min-h-screen">
        <div className="competenceHeader max-w-full basis-full flex-flex-xs-12 px-2 py-5 text-center text-very-dark-gray">
          <h1 className="text-32px font-black uppercase tracking-widest my-2.5 relative">
            <i className="fa-solid fa-wallet"></i> Portfolio
          </h1>
          <p className="leading-7 tracking-wider font-light text-sm">
            Bienvenue dans mon portfolio artistique !  travers
            cette collection d'uvres, je vous invite  explorer mon univers. Chaque projet prsent ici est le fruit d'un engagement profond, et j'espre qu'ils vous captiveront autant qu'ils m'ont inspir  les dvelopper. Que cette visite vous offre un aperu de ma vision et suscite en vous une connexion avec mon travail.
          </p>
        </div>
        <div className="mt-9 flex flex-row flex-wrap">
          {portfolioData.map((data) => {
            const { image, img_desc, nom, description, technos, vue } = data;
            return (
              <PortfolioComponent
                key={data.id}
                image={image}
                img_desc={img_desc}
                nom={nom}
                description={description}
                technos={technos}
                vue={vue}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Portfolio;
